import React, { useState } from "react";
import { Send, Upload } from "lucide-react";

export default function Chatbot() {
  const [file, setFile] = useState(null);
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");

  const handleFileUpload = (event) => {
    setFile(event.target.files[0]);
  };

  const handleSendMessage = () => {
    if (input.trim()) {
      setMessages([...messages, { text: input, sender: "user" }]);
      setInput("");
    }
  };

  return (
    <div className="max-w-lg mx-auto p-6 bg-white rounded-xl shadow-lg">
      <div className="mb-4">
        <input
          type="file"
          onChange={handleFileUpload}
          className="hidden"
          id="fileUpload"
        />
        <label
          htmlFor="fileUpload"
          className="cursor-pointer flex items-center gap-2 text-blue-500 hover:text-blue-700"
        >
          <Upload size={20} /> Upload Document
        </label>
        {file && (
          <p className="mt-2 text-sm text-gray-500">Uploaded: {file.name}</p>
        )}
      </div>

      <div className="border p-3 h-64 overflow-auto bg-gray-100 rounded-md">
        {messages.map((msg, index) => (
          <div
            key={index}
            className={`p-2 my-1 rounded-md ${
              msg.sender === "user"
                ? "bg-blue-500 text-white self-end"
                : "bg-gray-300 text-black"
            }`}
          >
            {msg.text}
          </div>
        ))}
      </div>

      <div className="flex items-center gap-2 mt-3">
        <input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Ask something..."
          className="border p-2 flex-1 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          onClick={handleSendMessage}
          className="p-2 bg-blue-500 text-white rounded-md flex items-center gap-2 hover:bg-blue-600"
        >
          <Send size={16} /> Send
        </button>
      </div>
    </div>
  );
}
